@media (max-width: 1200px) {
  .container {
    max-width: 960px; } }

@media (max-width: 992px) {
  .container {
    max-width: 720px; }
  .header {
    padding: 30px 0;
    &__logo {
      margin-right: 48px; }
    &__search {
      width: 340px; } }
  .nav {
    padding: 19px 0 23px;
    &__list {
      justify-content: space-around; }
    &__link {
      font-size: 12px; } }
  .logo {
    &__img {
      width: 132px; } }
  .hotel {
    &__inner {
      margin-bottom: 0; }
    &__rating {
      margin-top: 7px; } } }
@media (max-width: 767px) {
  .container {
    max-width: 540px; }
  .nav {
    display: none; }
  .header {
    &__search {
      display: none; }
    &__user {
      display: none; }
    &__burger {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: space-between;
      width: 16px;
      height: 11px;
      padding: 0; }
    &__line {
      width: 16px;
      height: 1px;
      background-color: $primary-color; } }
  .logo {
    &__img {
      width: 132px; } }
  .hotel {
    &__inner {
      justify-content: space-between; }
    &__text {
      width: 100%; }
    &__stars {
      flex-basis: auto; }
    &__title {
      flex-basis: 100%;
      margin-top: 10px;
      margin-right: 0; }
    &__offer {
      order: -1; }
    &__rating {
      display: none; } } }
@media (max-width: 576px) {
  .container {
    padding: 0;
    width: 87%; } }
@media (max-width: 480px) {
  .header {
    padding: 21px 0 22px; }
  .logo {
    &__img {
      width: 99px; } }
  .breadcrumb {
    padding: 20px 0; }
  .hotel {
    padding-top: 3px;
    &__title {
      font-size: 24px; } } }

