  font-family: 'Mulish';
  src: url("../fonts/Mulish-SemiBold.woff2") format("woff2"), url("../fonts/Mulish-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Nunito';
  src: url("../fonts/Nunito-Regular.woff2") format("woff2"), url("../fonts/Nunito-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Nunito';
  src: url("../fonts/Nunito-SemiBold.woff2") format("woff2"), url("../fonts/Nunito-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Nunito';
  src: url("../fonts/Nunito-ExtraBold.woff2") format("woff2"), url("../fonts/Nunito-ExtraBold.woff") format("woff");
  font-weight: 800;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Mulish';
  src: url("../fonts/Mulish-Bold.woff2") format("woff2"), url("../fonts/Mulish-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Mulish';
  src: url("../fonts/Mulish-Light.woff2") format("woff2"), url("../fonts/Mulish-Light.woff") format("woff");
  font-weight: 300;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Mulish';
  src: url("../fonts/Mulish-Regular.woff2") format("woff2"), url("../fonts/Mulish-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "Mulish", sans-serif;
  background-color: #F5FAFE;
}

h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul, ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

button {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 10px;
  max-width: 1130px;
}

.error {
  border: 2px #ff4019 solid !important;
}

.is-hidden {
  display: none !important;
}

.header {
  background-color: #fff;
  padding: 40px 0;
}

.header__top {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.header__logo {
  margin-right: 108px;
}

.header__search {
  margin-right: auto;
}

.header__burger {
  display: none;
}

.logo__img {
  width: 177px;
  height: 24px;
}

.search {
  position: relative;
  width: 350px;
  overflow: hidden;
  border-radius: 6px;
}

.search__field {
  position: relative;
  width: 100%;
  padding: 11px 75px 11px 23px;
  background: #F5FAFE;
  border: none;
  transition: opacity 0.2s;
}

.search__field:focus + .search__placeholder {
  opacity: 0;
  transition: opacity 0.2s;
}

.search__placeholder {
  position: absolute;
  left: 23px;
  top: 50%;
  transform: translateY(-50%);
  font-size: 12px;
  font-weight: 400;
  color: #333;
  opacity: 1;
  transition: opacity 0.2s;
}

.search__placeholder::after {
  content: '';
  position: absolute;
  right: -5px;
  top: 0;
  background-color: #333;
  height: 100%;
  width: 1px;
  transition: opacity 0.2s;
  animation: 0.7s infinite place;
}

.search__button {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  width: 65px;
  background: #EC1F46;
}

.search__img {
  width: 16px;
  height: 16px;
  fill: #fff;
}

.user {
  display: flex;
  align-items: center;
}

.user__avatar {
  margin-right: 18px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}

.user__name {
  font-size: 14px;
  font-weight: 400;
  color: #333;
}

@keyframes place {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

.nav {
  padding: 31px 0;
  background-color: #0a223d;
}

.nav__list {
  display: flex;
  justify-content: space-between;
}

.nav__link {
  font-weight: 700;
  font-size: 14px;
  text-transform: uppercase;
  color: #fff;
}

.breadcrumb {
  padding: 19px 0;
}

.breadcrumb__list {
  display: flex;
  align-items: center;
  font-size: 10px;
  color: #333;
}

.breadcrumb__item:not(:last-child):after {
  content: "/";
  margin-right: 4px;
}

.hotel {
  padding-top: 17px;
  padding-bottom: 70px;
}

.hotel__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 18px;
}

.hotel__inner {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 2px;
}

.hotel__stars {
  flex-basis: 100%;
  margin-bottom: 4px;
}

.hotel__stars-img {
  width: 8px;
  height: 8px;
}

.hotel__title {
  font-weight: 700;
  font-size: 24px;
  color: #333;
  text-transform: uppercase;
  margin-right: 15px;
}

.hotel__offer {
  padding: 5px 9px;
  color: #fff;
  font-weight: 600;
  font-size: 10px;
  background-color: #EC1F46;
  border-radius: 3px;
}

.hotel__descr {
  font-size: 12px;
  line-height: 15px;
  color: #333;
}

.hotel__rating {
  margin-top: 5px;
}

.hotel__grid {
  display: flex;
  justify-content: space-between;
}

.hotel__slider {
  position: relative;
  overflow: hidden;
  width: 100%;
  margin-right: 30px;
  height: 450px;
  border-radius: 8px;
  margin-left: 0;
}

.hotel__slider-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 30px;
  height: 30px;
  background-position: center;
  background-size: 10px;
  background-repeat: no-repeat;
  z-index: 99;
}

.hotel__slider-btn--next {
  right: 16px;
  background-image: url("../img/btn-next.svg");
}

.hotel__slider-btn--prev {
  left: 18px;
  background-image: url("../img/btn-prev.svg");
}

.hotel__slide {
  position: relative;
}

.hotel__slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hotel__slide:after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(0, 122, 223, 0.48) 0%, rgba(255, 255, 255, 0.128) 100%);
  z-index: 10;
}

.hotel__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.hotel__booking {
  font-family: 'Nunito', sans-serif;
  display: flex;
  padding: 32px 28px 26px 27px;
  width: 350px;
  min-height: 210px;
  background-color: #0a223d;
  color: #fff;
  border-radius: 8px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.hotel__price {
  display: flex;
  flex-direction: column;
}

.hotel__starts {
  font-size: 10px;
  font-weight: 400;
  margin-bottom: 4px;
}

.hotel__pricetag {
  font-size: 24px;
  font-weight: 800;
}

.hotel__how {
  font-size: 12px;
  font-weight: 400;
}

.hotel__button {
  align-self: flex-end;
  padding: 13px 30px;
  width: 100%;
  min-height: 45px;
  font-weight: 600;
  font-size: 14px;
  color: inherit;
  background-color: #EC1F46;
  border-radius: 4px;
}

.hotel__room {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin-top: 3px;
  font-size: 12px;
  font-weight: 400;
}

.hotel__room-text {
  position: relative;
}

.hotel__room-text:first-child {
  margin-bottom: 11px;
}

.hotel__room-text:first-child::after {
  content: '';
  position: absolute;
  left: -25px;
  top: 0;
  width: 14px;
  height: 14px;
  background-image: url("../img/guest.svg");
}

.hotel__room-text:last-child::after {
  content: '';
  position: absolute;
  left: -27px;
  top: -1px;
  width: 14px;
  height: 14px;
  background-image: url("../img/home.svg");
}

.hotel__call {
  display: flex;
  justify-content: space-between;
  margin-top: 2px;
  width: 100%;
  font-weight: 600;
  font-size: 18px;
}

.hotel__call-text {
  display: inline-block;
}

.hotel__call-link {
  position: relative;
  display: inline-block;
}

.hotel__call-link::after {
  content: '';
  position: absolute;
  left: -23px;
  top: 4px;
  width: 14px;
  height: 14px;
  background-image: url("../img/call.svg");
}

.hotel__map {
  width: 347px;
  height: 213px;
  background-color: #EC1F46;
  border-radius: 10px;
}

.hotel__map-frame {
  width: 100%;
  height: 100%;
}

.rating {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 65px;
  min-height: 45px;
  padding: 7px;
  background-color: #EC1F46;
  border-radius: 4px;
  color: #fff;
}

.rating__name {
  margin-bottom: 3px;
  font-size: 8px;
}

.rating__counter {
  font-size: 14px;
}

@media (max-width: 1200px) {
  .container {
    max-width: 960px;
  }
}

@media (max-width: 992px) {
  .container {
    max-width: 720px;
  }
  .header {
    padding: 30px 0;
  }
  .header__logo {
    margin-right: 48px;
  }
  .header__search {
    width: 340px;
  }
  .nav {
    padding: 19px 0 23px;
  }
  .nav__list {
    justify-content: space-around;
  }
  .nav__link {
    font-size: 12px;
  }
  .logo__img {
    width: 132px;
  }
  .hotel__inner {
    margin-bottom: 0;
  }
  .hotel__rating {
    margin-top: 7px;
  }
}

@media (max-width: 767px) {
  .container {
    max-width: 540px;
  }
  .nav {
    display: none;
  }
  .header__search {
    display: none;
  }
  .header__user {
    display: none;
  }
  .header__burger {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    width: 16px;
    height: 11px;
    padding: 0;
  }
  .header__line {
    width: 16px;
    height: 1px;
    background-color: #333;
  }
  .logo__img {
    width: 132px;
  }
  .hotel__inner {
    justify-content: space-between;
  }
  .hotel__text {
    width: 100%;
  }
  .hotel__stars {
    flex-basis: auto;
  }
  .hotel__title {
    flex-basis: 100%;
    margin-top: 10px;
    margin-right: 0;
  }
  .hotel__offer {
    order: -1;
  }
  .hotel__rating {
    display: none;
  }
}

@media (max-width: 576px) {
  .container {
    padding: 0;
    width: 87%;
  }
}

@media (max-width: 480px) {
  .header {
    padding: 21px 0 22px;
  }
  .logo__img {
    width: 99px;
  }
  .breadcrumb {
    padding: 20px 0;
  }
  .hotel {
    padding-top: 3px;
  }
  .hotel__title {
    font-size: 24px;
  }
}
